import {UniSwipeAction} from 'components/uni/uni-swipe-action/uni-swipe-action'
import {UniLoadMore} from 'components/uni/uni-load-more/uni-load-more'
import {UniPopup} from 'components/uni/uni-popup/uni-popup'
import {KsEmpty} from 'components/ks-components/empty/empty'
import {GetUserMessageReciveList,DoUserSiteMsg,GetOneSiteMsgInfo} from 'api/user'
export default{
	components:{
		UniPopup,
		KsEmpty,
		UniLoadMore,
		UniSwipeAction
	},
	data(){
		return {
			options:[
				{
			        text: '删除',
			        style: {
			            backgroundColor: '#FF3B30'
			        }
			    }    
			],
			page:1,
			list:[],
			pageCount:0,
			moreStatus:"",
			data:{},
			show:false,
		}
	},
	onLoad() {
		this.getList()
	},
	onPullDownRefresh() {
		this.getList() 
	},
	/*滚动加载*/
	onReachBottom(){
		this.page++
		if(this.page<=this.pageCount){
			this.getList(this.page)
		}	
	},
	methods:{
		close(e){
			this.show = e.show
			console.log(e)
		},
		getList(p=1){
			this.page = p
			if(this.page==1){
				this.list = []
			}
			this.moreStatus = 'loading'
			GetUserMessageReciveList({
				page:this.page,
				pagesize:10,
			}).then(res=>{
				uni.stopPullDownRefresh();
				this.pageCount = res.pageCount
				if(this.page >= this.pageCount){
					this.moreStatus = 'noMore'
				}else{
					this.moreStatus = 'more'
				}
				if(res.result){
					this.list = this.list.concat(res.data)
				}
			})		
		},
		open(item){
			
			GetOneSiteMsgInfo({
			    actiontype: 0,
			    id: item.id,
			}).then(res=> {
				this.show = true
			    if (res.result) {
			    	this.data = res.data[0]
					this.$store.dispatch('getUserInfo').then(_=>{
						this.getList()	
					})
				}
			})
		},            
		delItem(item) {
			console.log(item)
			uni.showModal({
			    title: '',
			    content: '确定删除吗？',
			    success(res) {
			        if (res.confirm) {
			            DoUserSiteMsg({
			                actiontype: 0,
			                do: 'delete',
			                ids: item.id,
			            }).then(res=> {
			                if (res.result) {
			                    this.getList()
			                } else {
			                    uni.showToast({
			                    	title: res.msg,
			                    	icon:none
			                    })
			                }
			            })
			        }
			    }
			});
			    
		},
	}
}
